@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
.main {
  width: 350px;
  border-radius: 20px;
  background-color: rgba(0, 0, 0, 0.8);
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0px 0px 10px 1px rgb(54, 54, 54);
}

.clear {
  width: 80px;
  height: 80px;
  margin-top: 35px;
}

.top {
  height: 60px;
  background-color: #7b34ff;
  color: rgb(241, 241, 241);
  padding: 10px;
  /* border-radius: 20px 20px 0 0; */
  font-family: "Raleway", sans-serif;
  display: flex;
  justify-content: space-between;
}

.header {
  color: rgb(241, 241, 241);
  margin: 10px 0px 0px 10px;
  font-size: 25px;
  /* border-radius: 20px 20px 0 0; */
  font-family: "Raleway", sans-serif;
}

.day {
  padding: 15px;
  color: rgb(241, 241, 241);
  font-family: "Raleway", sans-serif;
  font-size: 24px;
  font-weight: 600;
  height: 170px;
}

.temp {
  padding: 15px;
  color: rgb(241, 241, 241);
  font-family: "Raleway", sans-serif;
  font-size: 18px;
}

.divider {
  width: 70%;
  height: 2px;
  background-color: rgb(241, 241, 241);
  margin-left: auto;
  margin-right: auto;
  margin-top: 15px;
}

.flex {
  display: flex;
  width: 100%;
  justify-content: center;
  text-align: center;
}

.center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  width: 100%;
}

.sunrise-sunset {
  padding: 15px;
  color: rgb(241, 241, 241);
  font-family: "Raleway", sans-serif;
  font-size: 16px;
}

.description {
  color: rgb(241, 241, 241);
  font-family: "Raleway", sans-serif;
  font-size: 24px;
  font-weight: 600;
}

.button {
  width: 35px;
  height: 35px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Raleway", sans-serif;
}

main {
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.75)
  );
  min-height: 100vh;
}

.App {
  background: url(/weatherly/static/media/seattle1.a16b3eec.jpg);
  background-size: cover;
  background-position: center;
  /* background-size: cover;
  background-position: botoom;
  transition: 0.4s ease-out; */
  height: 100vh;
}

.App.night {
  background-color: rgb(14, 8, 71);
}

.Wrapper {
  display: flex;
  width: 100%;
  justify-content: center;
}

.searchbox {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px 0px 75px 0px;
}

.searchbar {
  display: block;
  width: 77%;
  padding: 15px;

  -webkit-appearance: none;

     -moz-appearance: none;

          appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.7);

  box-shadow: 0px 5px rgba(0, 0, 0, 15%);

  color: #313131;
  font-size: 18px;
}

.searchbar:focus {
  background-color: rgba(255, 255, 255, 0.9);
}

.search-btn {
  width: 50px;
  height: 50px;
  border-radius: 10px;
  margin-top: 10px;
  font-size: 32px;
  border: none;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 15%);
  transition: ease-in-out 0.2s;
}

.search-btn:hover {
  background-color: rgba(0, 0, 0, 30%);
  color: #fff;
  transition: ease-in-out 0.2s;
}

